    import { mapState } from "vuex";

    export default {
        data() {
            return {
		        auto: false,
		        list: [],
		        index: 0,
		        level: 0,
		        other: 0,
		        current: 'wechat',
		        detail: {
		        	price: '',
		        	money: ''
		        }
            }
        },
        name: "upgrade",
        computed: {
            ...mapState({
                memberImg: state => state.mallConfig.__wxapp_img.member,
            })
        },
        methods: {
            payMember(member_level) {
		        let that = this;
				uni.showLoading({
					mask: true,
                    title: '领取中...'
                });
                that.$request({
                    url: that.$api.member.purchase,
                    data: {
                        member_level: member_level
                    },
            		method: 'post'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        if (response.data.retry && response.data.retry === 1) {
                            that.getPayDataTimer = setTimeout(() => {
                                that.payMember(member_level);
                            }, 1000);
                        } else {
                            that.pay(response.data.pay_id,member_level);
                        }
                    } else {
                        uni.showModal({
                            title: '提示',
                            content: response.msg,
                            showCancel: false,
                        });
                    }
                }).catch(e => {
                    uni.hideLoading();
                    uni.showModal({
                        title: '提示',
                        content: e.errMsg,
                        showCancel: false,
                    });
                });
            },

            pay(id,level) {
            	let that = this;
                that.$payment.pay(id).then(res => {
                    uni.showToast({
                        title: '支付成功',
                        duration: 1000
                    });
	        		that.level = level;
                }).catch(res => {
                    uni.showToast({
                        title: '支付失败',
                        icon: 'none',
                        duration: 1000
                    });
                });
            },

		    change(e) {
		        let that = this;
		        that.index = e.detail.current;
		        that.detail = that.list[that.index];
		    },

            getList() {
                let that = this;
                that.$request({
                    url: that.$api.member.all,
                    method: 'get',
                }).then(response=>{
                    uni.hideLoading();
                    that.$hideLoading();
                    if(response.code == 0) {
		                that.list = response.data.list;
		                that.index = 0;
		                if (that.other > 0) {
		                    for (let i = 0; i < that.list.length; i++) {
		                        if (that.other == that.list[i].level) {
		                            that.index = i;
		                        }
		                    }
		                }else {
		                	for (let i = 0; i < that.list.length; i++) {
		                        if (that.level == that.list[i].level) {
		                            that.index = i;
		                        }
		                    }
		                }
		                that.detail = that.list[that.index];
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    that.$hideLoading();
                });
            },
        },
        onLoad(options) { this.$commonLoad.onload(options);
	        let that = this;
	        if (options.level) {
	            that.level = options.level
	        }
	        if (options.other) {
	            that.other = options.other
	        }
            that.$showLoading({
                text: '加载中...'
            });
	        that.getList();
	    },
    }
